import * as React from 'react';
import logo from '../assets/logo.png';


interface IHelpFeedbackProps{
  percentage: number
}

export class HelpFeedback extends React.Component <IHelpFeedbackProps, any> {

  public render() {

    return (
      <React.Fragment>
        <div style={{display: "flex", flexDirection: "column", alignItems:"flex-start"}}>
          <img style={{padding: "50px", width:'150px', height: "auto", alignSelf: "center"}}src={logo}/>
          <div style={{alignSelf: "center"}}>A sua chance de largar : {this.props.percentage*100}%</div>
          {this.props.percentage > 0.5 ?
          <React.Fragment>
          <div style={{display: "flex", alignSelf: 'center', paddingTop: '20px'}}>
              Constatamos que voc pode necessitar de auxlio nas reas...
          </div> 
          <div style={{display: "flex", fontSize: '40px', marginLeft: '50px'}}>
            Emocionais:
          </div>
          <ul style={{fontSize: '20px'}}>
            <li>Procure a Frente Universitria de Sade Mental (FUSM): Contatos:</li>
            <li>Workshop com profissionais da sade. Links: </li>
            <li>Indicao de profissionais. Links:</li>
          </ul>
          <div style={{display: "flex", fontSize: '40px', marginLeft: '50px'}}>
            Financeiros:
          </div>
          <ul style={{fontSize: '20px'}}>
            <li>Bolsas de permanncia disponveis:</li>
            <li>Projetos remunerados da USP:</li>
            <li>Contato do SAS - Servio de Assistncia Social</li>
          </ul>
          <div style={{display: "flex", fontSize: '40px', marginLeft: '50px'}}>
            Acadmico:
          </div>
          <ul style={{fontSize: '20px'}}>
            <li>Agendamento de aulas de reforo: Links:</li>
          </ul>
          </React.Fragment>
          :
          <div style={{alignSelf: 'center'}}>
            Obrigado, seus dados nos ajudaro em pesquisas futuras!
          </div>
        }
        </div>
      </React.Fragment>
    );
  }

}



