import * as React from 'react';
import {ItalicText, Button, StandardRightText, CardWrapper } from '../App.style';
import logo from '../assets/logo.png';

interface IIntroductionProps {
    onClick: () => void;
}

export class Introduction extends React.Component <IIntroductionProps, any> {
  
  public render() {

    return (
      <React.Fragment>
        <div style={{display: "flex", flexDirection: "column", alignItems:"center"}}>
            <img style={{padding: "50px", width:'150px', height: "auto", alignSelf: "center"}}src={logo}/>
            <CardWrapper>
            <div style={{fontSize: 40, paddingTop:"20px", marginLeft: "50px", marginBottom: "50px", alignSelf:'flex-start'}}>Ajuda</div>
            <div style={{display:"flex", flexDirection: "row", justifyContent: "space-around"}}>
                <ItalicText>{" \"Talvez o problema no seja voc, as vezes  difcil seguir em frente \nMas se voc se sente assim, saiba que voc no est sozinho e que h soluo\""}</ItalicText>                                                                 
                <StandardRightText>Criamos um formulrio para compreender melhor estas dificuldades e proporcionar
                    auxlio de uma forma humana e simples.
                </StandardRightText> 
            </div>
            <div style={{padding: "50px"}}>
                <Button onClick={this.props.onClick} className="btn btn-info" role="button">Formulrio</Button>
            </div>
            </CardWrapper>
        </div>
      </React.Fragment>
    );
  }

}



