import * as React from 'react';
import { Button, CardWrapper } from '../App.style';
import logo from '../assets/logo.png';

interface ISummaryProps {
    onClick: () => void;
}

export class Summary extends React.Component <ISummaryProps, any> {
  
  public render() {

    return (
      <React.Fragment>
        <div style={{display: "flex", flexDirection: "column", alignItems:"flex-start"}}>
          <img style={{padding: "50px", width:'150px', height: "auto", alignSelf: "center"}}src={logo}/>

          <CardWrapper>

            <div style={{alignSelf: 'center', fontSize: '30px', padding: '20px', paddingTop: '35px'}}>
              Na graduao passamos por momentos difceis
            </div>
            <div style={{alignSelf: 'center', fontSize: '20px', padding: '20px', width: '90%', textJustify: 'auto', textAlign: 'justify'}}>
              Lovelace busca auxiliar universitrios com dificuldades de qualquer natureza na graduao. O formulrio a seguir  completamente annimo e visa coletar dados para auxiliar alunos de forma individual e coletiva para resolver problemas emocionais, acadmicos e financeiros dos universitrios. Mesmo para aqueles que no consideram ter grandes problemas na graduao, a sua participao nessa iniciativa nos ajuda a ajudar quem mais precisa.
            </div>
            <div className="col-sm-12" style={{alignSelf: 'center', padding: '20px'}}>
              <Button onClick={this.props.onClick}className="btn btn-info" role="button">AJUDA</Button>
            </div>
          </CardWrapper>
          
        </div>
      </React.Fragment>
    );
  }

}



